/**
 * Reset some basic elements
 */
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
hr,
dl,
dd,
ol,
ul,
figure {
  margin: 0;
  padding: 0;
}



/**
 * Basic styling
 */
body {
  font: $base-font-weight #{$base-font-size}/#{$base-line-height} $base-font-family;
  color: $text-color;
  background-color: $background-color;
  -webkit-text-size-adjust: 100%;
  -webkit-font-feature-settings: "kern" 1;
  -moz-font-feature-settings: "kern" 1;
  -o-font-feature-settings: "kern" 1;
  font-feature-settings: "kern" 1;
  font-kerning: normal;
}



/**
 * Set `margin-bottom` to maintain vertical rhythm
 */
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
ul,
ol,
dl,
figure,
%vertical-rhythm {
  margin-bottom: calc($spacing-unit / 2);
}



/**
 * Images
 */
img {
  max-width: 100%;
  vertical-align: middle;
}



/**
 * Figures
 */
figure>img {
  display: block;
}

figcaption {
  font-size: $small-font-size;
}



/**
 * Lists
 */
ul,
ol {
  margin-left: $spacing-unit;
}

li {

  >ul,
  >ol {
    margin-bottom: 0;
  }
}



/**
 * Headings
 */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: $base-font-weight;
}



/**
 * Links
 */
a {
  color: $brand-color;
  text-decoration: none;

  &:visited {
    color: darken($brand-color, 15%);
  }

  &:hover {
    color: $text-color;
    text-decoration: underline;
  }

  .social-media-list &:hover {
    text-decoration: none;

    .username {
      text-decoration: underline;
    }
  }
}


/**
 * Blockquotes
 */
blockquote {
  color: $grey-color;
  border-left: 4px solid $grey-color-light;
  padding-left: calc($spacing-unit / 2);
  @include relative-font-size(1.125);
  letter-spacing: -1px;
  font-style: italic;

  > :last-child {
    margin-bottom: 0;
  }
}



/**
 * Code formatting
 */
pre,
code {
  @include relative-font-size(0.9375);
  border: 1px solid $grey-color-light;
  border-radius: 3px;
  color: blue;
  background-color: white;
}

code {
  padding: 1px 5px;
}

pre {
  padding: 8px 12px;
  overflow-x: auto;

  >code {
    border: 0;
    padding-right: 0;
    padding-left: 0;
  }
}



/**
 * Wrapper
 */
.wrapper {
  padding-right: $spacing-unit;
  padding-left: $spacing-unit;
  @extend %clearfix;

  @include media-query($on-laptop) {
    max-width: -webkit-calc(#{$content-width} - (#{$spacing-unit}));
    max-width: calc(#{$content-width} - (#{$spacing-unit}));
    padding-right: calc($spacing-unit / 2);
    padding-left: calc($spacing-unit / 2);
  }
}



/**
 * Clearfix
 */
%clearfix:after {
  content: "";
  display: table;
  clear: both;
}



/**
 * Icons
 */
.icon>svg {
  display: inline-block;
  vertical-align: middle;

  path {
    fill: $grey-color;
  }
}

.social-media-list {
  .icon {
    padding-right: 5px;
  }

  li+li {
    padding-top: 5px;
  }
}

.footer-heading>a:visited {
  color: black;
}






* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}

a {
  text-decoration: none;
}

.main-header {
  background: linear-gradient(#3F94BF, #246485);
  padding: 5px;
  text-align: center;
  color: white;
  text-shadow: #222 0px -1px 1px;
  position: fixed;
  width: 100%;
  left: 0;
  transition: all 0.2s ease;

  a {
    position: absolute;
    left: 20px;
    top: 20px;
    color: white;
    font-size: 32px;
  }
}

.page-wrap {
  float: right;
  width: 30px;
  transition: width 0.3s ease;
  border: 1px solid #e8e8e8;
}

.main-nav {
  float: left;
  position: fixed;
  width: 0;
  overflow-y: auto;
  transition: width 0.3s ease;

  a {
    display: block;
    border-top: 1px solid #AFAFAF;
    border-bottom: 1px solid #AFAFAF;

    &:hover,
    &:focus {}
  }

  &:after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 34px;
  }
}

.close-menu {
  display: none;
}

.open-menu {
  display: block;
  margin: 5px 5px 0px 5px;
}

@media screen and (min-width: 520px) {
  .sideBar {
    width: 30%;
  }

  #main-nav {
    width: 30%;
    height: 80%;
    visibility: visible;
  }

  .main-nav a {
    padding: 2px;
  }

  #main-nav>ul>li>label {
    padding: 2px;
    background-color: #F7F7F7;
  }

  #main-nav>ul>li>label:hover {
    color: #111;
    text-decoration: underline;
  }

  .close-menu {
    visibility: hidden;
  }

  .open-menu {
    visibility: hidden;
  }
}

@media screen and (max-width: 519px) {
  .sideBar {
    width: 30px;
  }

  #main-nav {
    width: 30px;
    padding: 20px;
    visibility: hidden;
    height: 90%;
  }

  .main-nav a {
    padding: 0px;
  }

  .main-nav a {
    padding: 15px;
  }

  #main-nav>ul>li>label {
    padding: 15px;
    background-color: #F7F7F7;
  }

  .close-menu {
    visibility: visible;
  }

  .open-menu {
    visibility: visible;
  }
}

#main-nav:target {
  width: 90%;
  visibility: visible;
}

#main-nav:target+.page-wrap {
  width: 90%;

  .close-menu {
    left: 0;
    position: fixed;
    display: block;
    margin: 5px 5px 0px 5px;
  }

  .open-menu {
    display: none;
  }

  .main-header {
    width: 80%;
    left: 20%;
  }
}

.fa {
  display: inline !important;
}

.center-image {
  max-width: 400px;
  width: 50%;
  margin: 0 auto;
  display: block;
  border-radius: 5px;
}

@media screen and (max-width: 520px) {
  .center-image {
    width: 100%;
  }
}