 /* Collapsable menu without js
 --------------------------------------------- */
   
 nav a:before {
  content: ' ';
  margin-left: -0.5em;
  padding-left: 1em;
  white-space: pre;
}

 .toggle, [id^=drop] {
   display: none;
 }
 
 nav {
 margin: 0;
 padding: 0;
 background-color: #ffffff;
 }
 
 #logo {
 display: block;
 padding: 0 30px;
 float: left;
 font-size: 20px;
 line-height: 60px;
 }
 
 nav:after {
 content: "";
 display: table;
 clear: both;
 }
 
 nav ul {
 padding: 0;
 margin: 0;
 list-style: none;
 position: relative;
 }
 
 nav ul li {
 margin: 0px;
 display: inline-block;
 float: left;
 background-color: #ffffff;
 }
 
 nav a {
 display: block;
 padding: 0 20px;
 color: #2a7ae2;
 text-decoration: none;
 }
 
 nav ul li ul li:hover { background: #000000; }
 
 nav a:hover { background-color: #000000; }
 
 nav ul ul {
 display: none;
 position: absolute;
 top: 60px;
 }
 
 nav ul li:hover > ul { display: inherit; }
 
 nav ul ul li {
 width: 170px;
 float: none;
 display: list-item;
 position: relative;
 }
 
 nav ul ul ul li {
 position: relative;
 top: -60px;
 left: 170px;
 }
 
 li > a:after { content: ' +'; }
 
 li > a:only-child:after { content: ''; }
 
 
 /* Media Queries
 --------------------------------------------- */
 
 @media all  {
 
 #logo {
 display: block;
 padding: 0;
 width: 100%;
 text-align: center;
 float: none;
 }
 
 nav { margin: 0; }
 
 .toggle + a,
 .menu { 
  display: none; 
  visibility: hidden;
}
 
 .toggle {
 display: block;
 /* background-color: #ffffff; */
 padding: 0 20px;
 color: #000000;
 text-decoration: none;
 border: none;
 }
 
 .toggle:hover { background-color: #ffffff; }
 
 [id^=drop]:checked + ul { display: block; }
 
 nav ul li {
 display: block;
 width: 100%;
 }
 
 nav ul ul .toggle,
 nav ul ul a { padding: 0 40px; }
 
 nav ul ul ul a { padding: 0 80px; }
 
 nav a:hover,
 nav ul ul ul a { background-color: #1756a9; }
 
 nav ul li ul li .toggle,
 nav ul ul a { background-color: #ffffff; }
 
 nav ul ul {
 float: none;
 position: static;
 color: #2a7ae2;
 }
 
 nav ul ul li:hover > ul,
 nav ul li:hover > ul { display: none; }
 
 nav ul ul li {
 display: block;
 width: 100%;
 }
 
 nav ul ul ul li { position: static;
 
 }
 }
 
 @media all and (max-width : 330px) {
 
 nav ul li {
 display: block;
 width: 94%;
 }
 
 }